<template>
  <div class="inlinefrom-container">
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label="搜索关键字" >
        <el-input v-model="formInline.searchWord" placeholder="目前没用"></el-input>
      </el-form-item>
      <el-select
        v-model="formInline.category"
        clearable
        placeholder="请选择商品类目"
      >
        <el-option
          v-for="item in category"
          :key="item.id"
          :label="item.name"
          :value="item.id"
        >
        </el-option>
      </el-select>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">搜索</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import api from '@/api/product.js';
export default {
  data() {
    return {
      formInline: {
        searchWord: "",
        category: "",
      },
      category: []
    };
  },
  methods: {
    onSubmit() {
      this.$emit('category', this.formInline)
    },
  },
  async created() {
    await api.categoryList().then((res)=>{
      this.category = res.data.data;
    })
  }
};
</script>

<style scoped lang="less">
.inlinefrom-container {
  form {
    float: left;
    div {
      margin: 0 30px;
    }
  }
}
</style>