<template>
  <div class="table-container">
    <el-table :data="listdata" style="width: 100%" stripe>
      <el-table-column
        prop="id"
        label="ID"
        width="80px"
        show-overflow-tooltip
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="title"
        label="标题"
        show-overflow-tooltip
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="desc"
        label="描述"
        show-overflow-tooltip
        align="center"
      >
      </el-table-column>
      <el-table-column prop="category" label="类目" align="center">
      </el-table-column>
      <!-- <el-table-column prop="c_items" label="子类目"> </el-table-column> -->
      <el-table-column
        prop="tags"
        label="标签"
        show-overflow-tooltip
        align="center"
      >
      </el-table-column>
      <el-table-column prop="price" label="价格" align="center">
      </el-table-column>
      <el-table-column prop="price_off" label="折扣价" align="center">
      </el-table-column>
      <!-- <el-table-column prop="unit" label="单位" align="center"> </el-table-column> -->
      <el-table-column prop="status" label="上架状态" align="center">
      </el-table-column>
      <el-table-column prop="inventory" label="库存量" align="center">
      </el-table-column>
      <!-- <el-table-column prop="updateTime" label="更新时间" align="center" show-overflow-tooltip> </el-table-column> -->
      <el-table-column label="操作" width="150px" align="center">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">
            编辑
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import api from "@/api/product.js";
export default {
  props: ["Data"],
  data() {
    return {
      // tableData: [
      //   {
      //     _id: "0001",
      //     title: "biaoti",
      //     desc: "miaoshu",
      //     category: "shangpinliemu",
      //     c_items: ["liemu1"],
      //     tags: ["haodongxi", "daze"],
      //     price: 100,
      //     price_off: 80,
      //     unit: "yuan",
      //     status: 1,
      //     inventory: 25,
      //     updateTime: "2021-05-05",
      //   },
      // ],
      list: [],
      listdata: [],
    };
  },
  methods: {
    handleEdit(index) {
      //, row
      // console.log(index, row);
      // console.log(this.Data[index])
      this.$store.dispatch("editData", this.Data[index]);
      this.$router.push({ name: "edit" });
    },
    handleDelete(index, row) {
      console.log(row.id)
      if(row.id === null) {
        alert('没有ID，无法删除！')
        return
      }
      this.$confirm("确定删除此条信息吗？？？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          api.delete(row.id).then((res) => {
            if (res.status == "success") {
              this.$emit("remove", row, true);
              this.$message({
                type: "success",
                message: "删除成功!",
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
      // console.log(index, row);
    },
  },
  // computed: {
  //   tableData() {
  //     // if(!this.list){return;}
  //     console.log(typeof this.list)
  //     let _this = this;
  //     this.listdata = this.Data.map((item) => {
  //       // console.log(item);
  //       return {
  //         ...item,
  //         // id: item.id == null ? Math.floor(Math.random() * 1000) : item.id,
  //         status: item.status == 1 ? "上架" : "下架",
  //         // category: (function () {
  //         //   // console.log(item.category,_this.list)
  //         //   _this.list.data[item.category-1 < 0 ? 0: item.category-1].name
  //         // })()
  //         category: _this.list.data[item.category-1 < 0 ? 0: item.category-1].name
  //       };
  //     });
  //   },
  // },
  watch: {
    Data: function () {
      // console.log(this.Data);
      let _this = this;
      api.categoryList().then((res) => {
        this.list = res.data;
        this.listdata = this.Data.map((item) => {
          return {
            ...item,
            // id: item.id == null ? Math.floor(Math.random() * 1000) : item.id,
            status: item.status == 1 ? "上架" : "下架",
            category:
              _this.list.data[item.category - 1 < 0 ? 0 : item.category - 1]
                .name,
          };
        });
      });
    },
  },
  // async created() {
  //   // console.log(this.Data)
  //   let _this = this;
  //   await api.categoryList().then((res) => {
  //     this.list = res.data;
  //     this.listdata = this.Data.map((item) => {
  //       return {
  //         ...item,
  //         // id: item.id == null ? Math.floor(Math.random() * 1000) : item.id,
  //         status: item.status == 1 ? "上架" : "下架",
  //         category: _this.list.data[item.category-1 < 0 ? 0: item.category-1].name
  //       };
  //     });
  //   });
  // },
};
</script>

