<template>
  <div class="page-container">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[20, 40, 60, 80, 100]"
        :page-size="size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
  </div>
</template>

<script>
export default {
  props: {total:{type: Number},size:{type: Number}},
  methods: {
    handleSizeChange(val) {
      this.$emit('limit', val)
      console.log(val)
    },
    handleCurrentChange(val) {
      this.$emit('page', val)
    },
  },
  data() {
    return {
    };
  },
  computed: {
    currentPage(){
      return 1;
    }
  }
};
</script>

<style scoped lang="less">
.page-container{
    div{
        float: right;
        margin-top: 20px;
        height: 50px;
    }
}
</style>