<template>
  <div class="peoductList-container">
    <InlineFrom @category="category" />
    <Table :Data="Product.data" @remove="remove" />
    <Page :total="Product.total" :size="Size" @limit="limit" @page="page" />
  </div>
</template>

<script>
import InlineFrom from "@/components/View/Inlinefrom.vue";
import Table from "@/components/View/Table.vue";
import Page from "@/components/View/Page.vue";
import api from "@/api/product.js";
export default {
  components: {
    InlineFrom,
    Table,
    Page,
  },
  data() {
    return {
      Product: {
        data: [],
        total: 10,
      },
      Page: 1,
      Size: 20,
      SearchWord: "",
      Category: 0,
    };
  },
  methods: {
    limit(val) {
      this.Size = val;
      this.productList();
    },
    page(val) {
      this.Page = val;
      this.productList();
    },
    category(val) {
      // console.log(val)
      this.SearchWord = val.searchWord;
      this.Category = val.category;
      this.productList();
    },
    remove(data, type) {
      // console.log(data, type)
      if(type) {
        this.productList();
      }
    },
    productList() {
      api
        .list({
          page: this.Page,
          size: this.Size,
          searchWord: this.SearchWord,
          category: this.Category == 0 ? null : this.Category,
        })
        .then((res) => {
          this.Product.data = res.data.data;
          this.Product.total = res.data.total;
          // console.log(this.Product)
        });
    },
  },
  async created() {
    await this.productList();
  },
};
</script>

<style scoped lang="less">
.peoductList-container {
  height: 100%;
  // overflow: scroll;
}
</style>